import React, { useEffect, useState } from 'react'
import { Row, Col, Card, Button, Table } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { useSelector } from 'react-redux';
import withReactContent from 'sweetalert2-react-content';
import Aux from '../../../hoc/_Aux';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { TipoPresentacionProductoUpSert } from './TipoPresentacionProductoUpSert';
import { NoAutorizado } from '../base/NoAutorizado';
import Loading from '../base/Loading';
import { asignarEstiloTabla, limpiarEstiloTabla } from '../../../helpers/estiloTabla';
const menuId = 48;
export const ProductoTipoPresentacion = () => {
    const state = useSelector(state => state);
    const [loading, setLoading] = useState(true)
    const [accesos, setAccesos] = useState([]);
    const [abrirModal, setAbrirModal] = useState(false);

    const [catTipoPresentacion, setCatTipoPresentacion] = useState([]);
    const initData = {
        tipo_presentacionId: '',
        nombre: '',
        descripcion: '',
        estadoId: 1
    };

    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuId);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }

    const [dataInicial, setdataInicial] = useState(initData);
    const handleOpenModal = () => {
        setAbrirModal(true);
        setdataInicial(initData);
    }
    const GetListado = async () => {
        if (accesos.find(acceso => acceso.accesoId === 3)) {
            limpiarEstiloTabla("#tablaElemento");
            setLoading(true);
            let response = await callApi('tipopresentacion?estadoId=1;2');
            if (response) {
                setCatTipoPresentacion(response);
            }
            asignarEstiloTabla("#tablaElemento");

        }
        setLoading(false);
    }
    const handleEditar = (id) => {
        const { tipo_presentacionId, nombre, descripcion, estadoId } = catTipoPresentacion.find(item => item.tipo_presentacionId === id);
        setdataInicial({
            tipo_presentacionId,
            nombre,
            descripcion,
            estadoId
        });
        setAbrirModal(true);
    }
    const handleDelete = (id) => {
        const MySwal = withReactContent(Swal);
        MySwal.fire({
            title: 'Alerta?',
            text: 'Esta seguro que desea eliminar el elemento',
            type: 'warning',
            showCloseButton: true,
            showCancelButton: true
        }).then(async (willDelete) => {
            if (willDelete.value) {
                let method = 'DELETE';
                let response = await callApi(`tipopresentacion/${id}`, {
                    method
                });
                if (response) {
                    alert_exitoso(response);
                    let listActual = catTipoPresentacion.filter(item => item.tipo_presentacionId !== id);
                    setCatTipoPresentacion(listActual);
                }
            } else {
                alert_warning('No se elimin贸 ning煤n elemento');
            }
        });
    }
    useEffect(() => {
        GetAccesosByMenuId();
    }, [state?.accesos]);

    useEffect(() => {
        GetListado();
    }, [accesos]);
    return (
        accesos.length > 0 &&
        <Aux>
            <Row className='btn-page'>
                <Col sm={12}>
                    <Card>
                        <Card.Header>
                            <Card.Title as="h5">Tipos de Presentaci贸n</Card.Title>
                        </Card.Header>
                        <Card.Body>
                            {
                                loading === true ?
                                    <Loading />
                                    : <>
                                        <Row className="align-items-center m-l-0">
                                            <Col />
                                            <Col className="text-right">
                                                {
                                                    accesos.find(acceso => acceso.accesoId === 1) &&
                                                    <Button variant="success" className="btn-sm btn-round has-ripple" onClick={handleOpenModal}><i className="feather icon-plus" /> Agregar</Button>
                                                }
                                            </Col>
                                        </Row>
                                        {
                                            accesos.find(acceso => acceso.accesoId === 3) ?
                                                <Table striped hover responsive bordered id="tablaElemento">
                                                    <thead>
                                                        <tr>
                                                            <th>Codigo</th>
                                                            <th>Nombre</th>
                                                            <th>Descripci贸n</th>
                                                            <th>Estado</th>
                                                            {
                                                                accesos.find(acceso => acceso.accesoId === 2 || acceso.accesoId === 4) &&
                                                                <th></th>
                                                            }
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            catTipoPresentacion.map(({ tipo_presentacionId, nombre, descripcion, Estado: { descripcion: estado } }) => (
                                                                <tr key={tipo_presentacionId}>
                                                                    <td>{tipo_presentacionId}</td>
                                                                    <td>{nombre}</td>
                                                                    <td>{descripcion}</td>
                                                                    <td>{estado}</td>
                                                                    {
                                                                        accesos.find(acceso => acceso.accesoId === 2 || acceso.accesoId === 4) &&
                                                                        <td style={{ textAlign: "center" }}>
                                                                            {
                                                                                accesos.find(acceso => acceso.accesoId === 2) &&
                                                                                <button className="btn-icon btn btn-info btn-sm" onClick={() => { handleEditar(tipo_presentacionId) }}><i className="feather icon-edit" /></button>
                                                                            }
                                                                            {
                                                                                accesos.find(acceso => acceso.accesoId === 4) &&
                                                                                <button className="btn-icon btn btn-danger btn-sm" onClick={() => { handleDelete(tipo_presentacionId) }}><i className="feather icon-trash-2" /></button>
                                                                            }
                                                                        </td>
                                                                    }
                                                                </tr>
                                                            ))
                                                        }
                                                    </tbody>
                                                </Table>
                                                : <NoAutorizado />
                                        }
                                    </>
                            }
                            {
                                abrirModal === true &&
                                <TipoPresentacionProductoUpSert abrirModal={abrirModal} setAbrirModal={setAbrirModal} GetListado={GetListado} dataInicial={dataInicial} />
                            }
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}
